import React, { createContext, useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';

const AuthContext = createContext(null);

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
    const [currentUser, setCurrentUser] = useState(() => {
        const savedUser = localStorage.getItem('currentUser');
        return savedUser ? JSON.parse(savedUser) : null;
    });
    const navigate = useNavigate();
    
    const admins = [ 
        //관리자 예비 데이터들
        { id: 1, username: 'mainAdmin', password: 'admin123', role: 'Main Admin', subordinates: [2, 3, 4, 5, 6] },
        { id: 2, username: 'subAdmin1', password: 'sub123', role: 'Sub Admin', subordinates: [7, 8] },
        { id: 3, username: 'subAdmin2', password: 'sub123', role: 'Sub Admin', subordinates: [9, 10] },
        { id: 4, username: 'subAdmin3', password: 'sub123', role: 'Sub Admin', subordinates: [11, 12] },
        { id: 5, username: 'subAdmin4', password: 'sub123', role: 'Sub Admin', subordinates: [13, 14] },
        { id: 6, username: 'subAdmin5', password: 'sub123', role: 'Sub Admin', subordinates: [15, 16] },
        { id: 7, username: 'admin1', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 8, username: 'admin2', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 9, username: 'admin3', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 10, username: 'admin4', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 11, username: 'admin5', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 12, username: 'admin6', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 13, username: 'admin7', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 14, username: 'admin8', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 15, username: 'admin9', password: 'admin123', role: 'Admin', subordinates: [] },
        { id: 16, username: 'admin10', password: 'admin123', role: 'Admin', subordinates: [] },
    
        ];

    const login = (username, password) => {
        const user = admins.find(u => u.username === username && u.password === password);
        if (user) {
            setCurrentUser(user);
            localStorage.setItem('currentUser', JSON.stringify(user));
            return true;
        }
        return false;
    };

    const logout = () => {
        setCurrentUser(null);
        localStorage.removeItem('currentUser');
    };

    return (
        <AuthContext.Provider value={{ currentUser, login, logout , admins }}>
            {children}
        </AuthContext.Provider>
    );
};
