import React from 'react';
import { Accordion, AccordionSummary, AccordionDetails, Typography, Container, Box } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const faqData = [
  { question: "How do I reset my password?", answer: "You can reset your password by clicking on the 'Forgot Password' link on the login page." },
  { question: "How do I contact customer support?", answer: "You can contact customer support by emailing support@example.com or calling 123-456-7890." },
  { question: "What is your refund policy?", answer: "Our refund policy allows refunds within 30 days of purchase. Please visit our refund policy page for more details." },
];

const FaqSection = () => (
  <Container maxWidth="md">
    <Typography variant="h4" component="h2" gutterBottom>FAQ</Typography>
    {faqData.map((faq, index) => (
      <Accordion key={index}>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography variant="h6">{faq.question}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>{faq.answer}</Typography>
        </AccordionDetails>
      </Accordion>
    ))}
  </Container>
);

export default FaqSection;
