import React, { useEffect, useRef, useState } from 'react';
import axios from 'axios';

const KakaoMap = ({ location }) => {
  const mapRef = useRef(null);
  const [what3wordsAddress, setWhat3wordsAddress] = useState('');

  useEffect(() => {
    // location 객체가 올바르게 전달되었는지 확인
    if (!location || !location.lat || !location.lng) {
      console.error('Invalid location object:', location);
      return;
    }

    // What3Words API 호출
    const fetchWhat3WordsAddress = async () => {
      try {
        const response = await axios.get(
          `https://api.what3words.com/v3/convert-to-3wa?coordinates=${location.lat},${location.lng}&key=G5YMQ4R9`
        );
        console.log('What3Words API response:', response.data);
        setWhat3wordsAddress(response.data.words);
      } catch (error) {
        console.error('What3Words API error:', error);
      }
    };

    fetchWhat3WordsAddress();

    const script = document.createElement('script');
    script.async = true;
    script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=fcd63c39aa153bdbdcba286ed51db841&autoload=false`;
    document.head.appendChild(script);

    script.onload = () => {
      window.kakao.maps.load(() => {
        const container = mapRef.current;
        const options = {
          center: new window.kakao.maps.LatLng(location.lat, location.lng),
          level: 3
        };
        const map = new window.kakao.maps.Map(container, options);
        const markerPosition = new window.kakao.maps.LatLng(location.lat, location.lng);
        const marker = new window.kakao.maps.Marker({
          position: markerPosition
        });
        marker.setMap(map);

        const infowindow = new window.kakao.maps.InfoWindow({
          content: `<div style="padding:5px;">사고 정보<br/> What3Words: ${what3wordsAddress}</div>`
        });

        window.kakao.maps.event.addListener(marker, 'click', () => {
          infowindow.open(map, marker);
        });

        if (what3wordsAddress) {
          infowindow.setContent(`<div style="padding:5px;">사고 정보<br/> What3Words: ${what3wordsAddress}</div>`);
        }
      });
    };

    return () => {
      document.head.removeChild(script);
    };
  }, [location, what3wordsAddress]);

  return <div ref={mapRef} style={{ width: '100%', height: '400px' }} />;
};

export default KakaoMap;
