import React, { useState, useEffect } from 'react';
import { Paper, Box, Typography, Container, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, TablePagination, Button } from '@mui/material';
import { useParams } from 'react-router-dom';
import { useMembers } from '../../context/MemberContext';
import { useAuth } from '../../context/AuthProvider';

const AdminMembers = () => {
    const { adminId } = useParams();
    const { members } = useMembers();
    const { admins } = useAuth();

    const admin = admins.find(admin => admin.id === parseInt(adminId));
    const [visibleMembers, setVisibleMembers] = useState([]);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);

    useEffect(() => {
        if (admin) {
            const filteredMembers = members.filter(member => member.adminId === parseInt(adminId));
            setVisibleMembers(filteredMembers);
        }
    }, [admin, members, adminId]);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    return (
        <Container maxWidth="lg" sx={{ mt: 4 }}>
            <Paper style={{ padding: '20px' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
                    <Typography variant="h4">{admin ? admin.username : 'Admin'}의 회원 목록</Typography>
                </Box>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Name</TableCell>
                                <TableCell>Phone</TableCell>
                                <TableCell>Vehicle</TableCell>
                                <TableCell>Role</TableCell>
                                <TableCell>Accident</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {visibleMembers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((member) => (
                                <TableRow key={member.id}>
                                    <TableCell>{member.name}</TableCell>
                                    <TableCell>{member.phone}</TableCell>
                                    <TableCell>{member.vehicle}</TableCell>
                                    <TableCell>{member.role}</TableCell>
                                    <TableCell>{member.accident}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[5, 10, 25]}
                    component="div"
                    count={visibleMembers.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                />
            </Paper>
        </Container>
    );
};

export default AdminMembers;
