import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../context/AuthProvider';
import { TextField, Button, Paper, Container, Typography, Box } from '@mui/material';

const LoginPage = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const auth = useAuth();
    const navigate = useNavigate();



    const handleSubmit = (e) => {
      e.preventDefault();
      if (auth.login(username, password)) {
          const user = auth.currentUser;
          if (user && (user.role === 'Main Admin' || user.role === 'Sub Admin')) {
              navigate('/admins');
          } else {
              navigate('/members');
          }
      } else {
          alert('Invalid username or password');
      }
  };

    return (
      <Container component="main" maxWidth="xs">
        <Paper elevation={6} sx={{ mt: 8, padding: 4, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <Typography component="h1" variant="h5">
                Sign in
            </Typography>
            <Box component="form" onSubmit={handleSubmit} sx={{ mt: 1 }}>
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="username"
                    label="Username"
                    name="username"
                    autoComplete="username"
                    autoFocus
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                />
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    name="password"
                    label="Password"
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                >
                    Sign In
                </Button>
            </Box>
        </Paper>
    </Container>
  );
};

export default LoginPage;

