import React from 'react';
import {
    Dialog, 
    DialogActions,
    DialogContent, 
    DialogTitle, 
    Button, 
    TextField, 
    RadioGroup, 
    FormControlLabel, 
    Radio, 
    FormLabel, 
    FormControl, 
    Box
  } from '@mui/material';

const GenericDialog = ({ open, data, onClose, onSave, onChange, fields, title }) => {
  const currentData = data || { accident: '없음', accidentDetails: { date: '', location: { lat: '', lng: '' } } };

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>{currentData.id ? `Edit ${title}` : `Add ${title}`}</DialogTitle>
      <DialogContent>
        {fields.map(field => {
          if (field.type === 'radio') {
            return (
              <FormControl component="fieldset" key={field.name}>
                <FormLabel component="legend">{field.label}</FormLabel>
                <RadioGroup
                  row
                  aria-label={field.name}
                  name={field.name}
                  value={currentData[field.name]}
                  onChange={onChange(field.name)}
                >
                  {field.options.map(option => (
                    <FormControlLabel key={option} value={option} control={<Radio />} label={option} />
                  ))}
                </RadioGroup>
              </FormControl>
            );
          }
          if (field.dependent && currentData.accident !== '있음') {
            return null;
          }
          return (
            <TextField
              key={field.name}
              autoFocus={field.autoFocus || false}
              margin="dense"
              label={field.label}
              type={field.type || 'text'}
              fullWidth
              multiline={field.multiline || false}
              rows={field.rows || 1}
              variant="standard"
              value={field.type === 'number' ? parseFloat(currentData[field.name]) : currentData[field.name]}
              onChange={onChange(field.name)}
              InputLabelProps={field.type === 'date' ? { shrink: true } : {}}
            />
          );
        })}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
        <Button onClick={onSave}>Save</Button>
      </DialogActions>
    </Dialog>
  );
};

export default GenericDialog;
