import React, { useState, useEffect } from 'react';
import { Container, Grid, Card, CardContent,CardMedia , Typography, TextField, Pagination, Box } from '@mui/material';

const insuranceCompanies = [
  { id: 1, name: 'ABC Insurance', description: 'Comprehensive coverage and support.', imageUrl: 'https://via.placeholder.com/150/0000FF/808080?text=ABC+Insurance' },
  { id: 2, name: 'XYZ Insurance', description: 'Affordable and reliable insurance.', imageUrl: 'https://via.placeholder.com/150/FF0000/FFFFFF?text=XYZ+Insurance' },
  { id: 3, name: '123 Insurance', description: 'Protecting your assets with care.', imageUrl: 'https://via.placeholder.com/150/00FF00/000000?text=123+Insurance' },
  { id: 4, name: 'SafeGuard Insurance', description: 'Your trusted insurance partner.', imageUrl: 'https://via.placeholder.com/150/FFFF00/000000?text=SafeGuard+Insurance' },
  { id: 5, name: 'Secure Life Insurance', description: 'Life insurance that fits your needs.', imageUrl: 'https://via.placeholder.com/150/FF00FF/000000?text=Secure+Life+Insurance' },
  { id: 6, name: 'ProtectAll Insurance', description: 'Comprehensive and affordable plans.', imageUrl: 'https://via.placeholder.com/150/00FFFF/000000?text=ProtectAll+Insurance' },
  { id: 7, name: 'Guardian Insurance', description: 'Reliable coverage for all your needs.', imageUrl: 'https://via.placeholder.com/150/800080/FFFFFF?text=Guardian+Insurance' },
  { id: 8, name: 'Alpha Insurance', description: 'Insurance solutions you can trust.', imageUrl: 'https://via.placeholder.com/150/008080/FFFFFF?text=Alpha+Insurance' }
  // 추가 보험사 데이터를 여기에 넣어주세요
];

const InsurancePage = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [filteredCompanies, setFilteredCompanies] = useState(insuranceCompanies);

  const itemsPerPage = 6; // 페이지당 아이템 수

  useEffect(() => {
    setFilteredCompanies(
      insuranceCompanies.filter(company =>
        company.name.toLowerCase().includes(searchTerm.toLowerCase())
      )
    );
  }, [searchTerm]);

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  const paginatedCompanies = filteredCompanies.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const count = Math.ceil(filteredCompanies.length / itemsPerPage); // 총 페이지 수

  return (
    <Container maxWidth="lg" sx={{ mt: 4 }}>
      <TextField
        label="Search Insurance Companies"
        variant="outlined"
        fullWidth
        value={searchTerm}
        onChange={handleSearchChange}
        sx={{ mb: 4 }}
      />
      <Grid container spacing={4}>
        {paginatedCompanies.map((company) => (
          <Grid item key={company.id} xs={12} sm={6} md={4}>
            <Card>
              <CardMedia
                component="img"
                height="100"
                image={company.imageUrl}
                alt={company.name}
              />
              <CardContent>
                <Typography variant="h5" component="div">
                  {company.name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {company.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <Pagination
          count={count}
          page={currentPage}
          onChange={handlePageChange}
          color="primary"
          showFirstButton
          showLastButton
          sx={{ marginTop: 2, justifyContent: 'center', display: 'flex' }}
        />
      </Box>
    </Container>
  );
};

export default InsurancePage;
