import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, Drawer, List, ListItem, ListItemIcon, ListItemText, Typography, Menu, MenuItem, Avatar, Box  } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import LogoutIcon from '@mui/icons-material/Logout'
import GroupIcon from '@mui/icons-material/Group';
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AnnouncementIcon from '@mui/icons-material/Announcement';
import { useNavigate, Link as RouterLink } from 'react-router-dom';
import { useAuth } from '../context/AuthProvider';

const Dashboard = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [menuAnchorEl, setMenuAnchorEl] = useState(null);
  const { currentUser, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/login');
  };



  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const handleMenuOpen = (event) => {
    setMenuAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setMenuAnchorEl(null);
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" color="inherit" aria-label="menu" onClick={toggleDrawer}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" color="inherit">
            Main
          </Typography>
          {currentUser && (
            <Box sx={{ display: 'flex', alignItems: 'center', ml: 'auto' }}>
              <Typography   variant="h6" color="inherit" sx={{ mr: 2 }} onClick={handleMenuOpen}>
                {currentUser.username}
              </Typography>
              <Menu
                anchorEl={menuAnchorEl}
                open={Boolean(menuAnchorEl)}
                onClose={handleMenuClose}
              >
                <MenuItem component={RouterLink} to="/admin-info">
                  <ListItemIcon>
                    <AccountCircleIcon fontSize="small" />
                  </ListItemIcon>
                  <ListItemText primary="관리자 정보" />
                </MenuItem>
                <MenuItem onClick={handleLogout}>
                  <ListItemIcon>
                    <LogoutIcon fontSize="small" />
                  </ListItemIcon>
                  <ListItemText primary="로그아웃" />
                </MenuItem>
              </Menu>
            </Box>
          )}
        </Toolbar>
      </AppBar>
      <Drawer anchor="left" open={drawerOpen} onClose={() => setDrawerOpen(false)}>
        <List>
        {currentUser && (currentUser.role === 'Main Admin' || currentUser.role === 'Sub Admin') ? (
            <>
              <ListItem button component={RouterLink} to="/admins">
                <ListItemIcon><GroupIcon /></ListItemIcon>
                <ListItemText primary="관리자 목록" />
              </ListItem>
            </>
            ) : (
          <ListItem button component={RouterLink} to="/members">
            <ListItemIcon><GroupIcon  /></ListItemIcon>
            <ListItemText primary="회원 목록" />
          </ListItem>

        )}
          <ListItem button component={RouterLink} to="/insurance">
            <ListItemIcon><AccountBoxIcon /></ListItemIcon>
            <ListItemText primary="보험사" />
          </ListItem>
          <ListItem button component={RouterLink} to="/customer-service">
            <ListItemIcon><SupportAgentIcon  /></ListItemIcon>
            <ListItemText primary="고객센터" />
          </ListItem>
          <ListItem button component={RouterLink} to="/notice">
            <ListItemIcon><AnnouncementIcon  /></ListItemIcon>
            <ListItemText primary="공지사항 " />
          </ListItem>
        </List>
      </Drawer>
    </>
  );
};

export default Dashboard;
