import React, { useState } from 'react';
import { Paper, Box, Button, Snackbar, Container, Chip, Stack } from '@mui/material';
import GenericTable from '../../components/GenericTable';
import GenericDialog from '../../components/GenericDialog';
import SearchBox from '../../components/SearchBox';
import { useNavigate } from 'react-router-dom';
import { useMembers } from '../../context/MemberContext';
import { useAuth } from '../../context/AuthProvider';

const MemberList = () => {
  const { currentUser } = useAuth();
  const { members, pendingMembers, accidents, approveMember, addMember, updateMember, deleteMember, addAccident, deleteAccident } = useMembers();
  const [searchTerm, setSearchTerm] = useState('');
  const [searchType, setSearchType] = useState('name');
  const [open, setOpen] = useState(false);
  const [currentMember, setCurrentMember] = useState(null);
  const [alertOpen, setAlertOpen] = useState(false);
  const [accidentDialogOpen, setAccidentDialogOpen] = useState(false);
  const [currentAccident, setCurrentAccident] = useState({ member_id: '', location: '', date: '', insurance_company_id: '' });
  const [selectedPendingMembers, setSelectedPendingMembers] = useState([]);
  const navigate = useNavigate();

  const [filter, setFilter] = useState('all');

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value.toLowerCase());
  };

  const getAccidentCount = (member_id) => {
    return accidents.filter(accident => accident.member_id === member_id).length;
  };

  const getAccidentLabel = (member_id) => {
    const count = getAccidentCount(member_id);
    return count > 0 ? `${count}회` : '없음';
  };

  const handleAccidentDialogOpen = (member) => {
    setCurrentAccident({ ...currentAccident, member_id: member.id });
    setAccidentDialogOpen(true);
  };

  const handleAccidentDialogClose = () => {
    setAccidentDialogOpen(false);
  };

  const handleAccidentSave = () => {
    if (!currentAccident.location || !currentAccident.date) {
      setAlertOpen(true);
      return;
    }
    addAccident(currentAccident);
    setAccidentDialogOpen(false);
  };

  const handleAccidentChange = (prop) => (event) => {
    setCurrentAccident({ ...currentAccident, [prop]: event.target.value });
  };

  const handleViewAccidentDetails = (member) => {
    navigate(`/member-details/${member.id}`, { state: { member } });
  };

  const visibleMembers = currentUser && currentUser.role === 'Main Admin'
    ? [...members, ...pendingMembers]
    : [...members, ...pendingMembers].filter(member => member.adminId === (currentUser ? currentUser.id : null));

  const filteredMembers = filter === 'all' 
    ? visibleMembers
    : filter === 'approved' 
      ? visibleMembers.filter(member => members.includes(member)) 
      : visibleMembers.filter(member => pendingMembers.includes(member));

  const searchedMembers = filteredMembers.filter(member =>
    member[searchType].toString().toLowerCase().includes(searchTerm)
  );

  const handleClickOpen = (member) => {
    setCurrentMember(member || { name: '', phone: '', vehicle: '', role: '' });
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    if (!currentMember.name || !currentMember.phone) {
      setAlertOpen(true);
      return;
    }
    if (currentMember.id) {
      updateMember(currentMember);
    } else {
      addMember({ ...currentMember, adminId: currentUser ? currentUser.id : null });
    }
    setOpen(false);
  };

  const handleChange = (prop) => (event) => {
    setCurrentMember({ ...currentMember, [prop]: event.target.value });
  };

  const handleDelete = (id) => {
    deleteMember(id);
  };

  const handleApprove = (id) => {
    approveMember(id);
    setAlertOpen(true);
  };

  const handleViewDetails = (member) => {
    navigate(`/member-details/${member.id}`, { state: { member } });
  };

  const handleCheckboxChange = (event, memberId) => {
    if (event.target.checked) {
      setSelectedPendingMembers([...selectedPendingMembers, memberId]);
    } else {
      setSelectedPendingMembers(selectedPendingMembers.filter(id => id !== memberId));
    }
  };

  const handleApproveSelected = () => {
    selectedPendingMembers.forEach(id => approveMember(id));
    setSelectedPendingMembers([]);
  };
  
  const searchTypes = [
    { value: 'name', label: 'Name' },
    { value: 'id', label: 'ID' },
    { value: 'phone', label: 'Phone' },
    { value: 'vehicle', label: 'Vehicle' },
    { value: 'role', label: 'Role' },
  ];

  return (
    <Container maxWidth="lg" sx={{ mt: 4 }}>
      <Paper style={{ padding: '20px' }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
          <SearchBox
            searchTerm={searchTerm}
            setSearchTerm={setSearchTerm}
            searchType={searchType}
            setSearchType={setSearchType}
            searchTypes={searchTypes}
          />
          <Button variant="contained" color="primary" onClick={() => handleClickOpen()}>Add Member</Button>
        </Box>
        <Stack direction="row" spacing={1} sx={{ marginBottom: 2 }}>
          <Chip label="All" onClick={() => setFilter('all')} color={filter === 'all' ? 'primary' : 'default'} />
          <Chip label="회원" onClick={() => setFilter('approved')} color={filter === 'approved' ? 'primary' : 'default'} />
          <Chip label="승인대기" onClick={() => setFilter('pending')} color={filter === 'pending' ? 'primary' : 'default'} />
        </Stack>
        {filter === 'pending' && (
          <Button
            variant="contained"
            color="secondary"
            onClick={handleApproveSelected}
            disabled={selectedPendingMembers.length === 0}
            sx={{ mb: 2 }}
          >
            승인
          </Button>
        )}
        <GenericTable
          columns={[
            { id: 'name', label: 'Name' },
            { id: 'phone', label: 'Phone' },
            { id: 'id', label: 'ID' },
            { id: 'vehicle', label: 'Vehicle' },
            { id: 'role', label: 'Role' },
            {
              id: 'accident',
              label: 'Accident',
              render: (member) => (
                <Button
                  variant="outlined"
                  onClick={() => handleViewAccidentDetails(member)}
                  style={{
                    backgroundColor: getAccidentCount(member.id) > 0 ? '#F28080' : '#6A95A6',
                    color: 'white'
                  }}
                >
                  {getAccidentLabel(member.id)}
                </Button>
              )
            },
          ]}
          data={searchedMembers}
          onEdit={handleClickOpen}
          onDelete={handleDelete}
          showCheckboxes={filter === 'pending'}
          onCheckboxChange={handleCheckboxChange}
        />
        <GenericDialog
          open={open}
          data={currentMember}
          onClose={handleClose}
          onSave={handleSave}
          onChange={handleChange}
          fields={[
            { name: 'name', label: 'Name', autoFocus: true },
            { name: 'phone', label: 'Phone' },
            { name: 'vehicle', label: 'Vehicle' },
            { name: 'role', label: 'Role' },
          ]}
          title="Member"
        />
        <GenericDialog
          open={accidentDialogOpen}
          data={currentAccident}
          onClose={handleAccidentDialogClose}
          onSave={handleAccidentSave}
          onChange={handleAccidentChange}
          fields={[
            { name: 'location', label: 'Location', autoFocus: true },
            { name: 'date', label: 'Date', type: 'date' },
          ]}
          title="Add Accident"
        />
        <Snackbar open={alertOpen} autoHideDuration={6000} onClose={() => setAlertOpen(false)} message="Please fill in all required fields." />
      </Paper>
    </Container>
  );
};

export default MemberList;
