import React from 'react';
import { TextField, MenuItem, FormControl, InputLabel, Select, Box } from '@mui/material';

const SearchBox = ({ searchTerm, setSearchTerm, searchType, setSearchType, searchTypes }) => {
 
  const handleSearchTypeChange = (event) => {
    setSearchType(event.target.value);
  };

  const handleSearchTermChange = (event) => {
    setSearchTerm(event.target.value);
  };

  return (
    <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
      <FormControl size="small" sx={{ minWidth: 120 }}>
        <InputLabel id="search-type-label">Type</InputLabel>
        <Select
          labelId="search-type-label"
          id="search-type"
          value={searchType}
          label="Type"
          onChange={handleSearchTypeChange}
          size="small"
        >
          {searchTypes.map((type) => (
            <MenuItem key={type.value} value={type.value}>{type.label}</MenuItem>
          ))}
        </Select>
      </FormControl>
      <TextField
        label="Search"
        variant="outlined"
        size="small"
        fullWidth
        value={searchTerm}
        onChange={handleSearchTermChange}
      />
    </Box>
  );
};

export default SearchBox;

