import React, { useState } from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow, Box, IconButton, TableContainer, TablePagination , Checkbox } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

const GenericTable = ({ columns, data, onEdit, onDelete, itemsPerPage = 5, showCheckboxes = false, onCheckboxChange }) => {
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(itemsPerPage);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const dataToShow = data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

    return (
        <Box>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            {columns.map(column => (
                                <TableCell key={column.id}>{column.label}</TableCell>
                            ))}
                            <TableCell>Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                    	{dataToShow.map(item => (
                        <TableRow key={item.id}>
                          {showCheckboxes && (
                            <TableCell padding="checkbox">
                              <Checkbox onChange={(e) => onCheckboxChange(e, item.id)} />
                            </TableCell>
                            )}
                            {columns.map(column => (
                              <TableCell key={`${column.id}-${item.id}`}>
                                {column.render ? column.render(item) : item[column.id]}
                              </TableCell>
                              ))}
                              <TableCell>
																<IconButton edge="end" aria-label="edit" onClick={() => onEdit(item)}>
																	<EditIcon />
																</IconButton>
																<IconButton edge="end" aria-label="delete" onClick={() => onDelete(item.id)}>
																	<DeleteIcon />
																</IconButton>
                              </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={data.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
            />
        </Box>
    );
};

export default GenericTable;
