import React, { useState } from 'react';
import { Box, Button, TextField, Typography, MenuItem, Grid, Card, CardContent } from '@mui/material';
import { useAuth } from '../../context/AuthProvider';

const AdminInfo = () => {
  const { currentUser } = useAuth();
  const [username, setUsername] = useState(currentUser ? currentUser.username : '');
  const [role, setRole] = useState(currentUser ? currentUser.role : '');
  const [subordinates, setSubordinates] = useState(currentUser ? currentUser.subordinates : []);
  const [email, setEmail] = useState(currentUser ? currentUser.email : '');
  const [phone, setPhone] = useState(currentUser ? currentUser.phone : '');
  const [memberLimit, setMemberLimit] = useState(0);

  const handleSave = () => {
    // 여기서 저장 로직을 추가합니다.
    console.log('Saved:', { username, role, subordinates, email, phone, memberLimit });
  };

  return (
    <Box sx={{ p: 3 }}>
      <Card>
        <CardContent>
          <Typography variant="h4" gutterBottom>관리자 정보</Typography>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                label="아이디"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                fullWidth
                margin="normal"
                disabled
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="역할"
                value={role}
                onChange={(e) => setRole(e.target.value)}
                fullWidth
                margin="normal"
                disabled
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="이메일"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                fullWidth
                margin="normal"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="휴대폰 번호"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                fullWidth
                margin="normal"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="하위 관리자 접근권한"
                value={subordinates.join(', ')}
                fullWidth
                margin="normal"
                disabled
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                select
                label="회원 권한 수"
                value={memberLimit}
                onChange={(e) => setMemberLimit(e.target.value)}
                fullWidth
                margin="normal"
              >
                <MenuItem value={0}>0</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={15}>15</MenuItem>
                <MenuItem value={20}>20</MenuItem>
              </TextField>
            </Grid>
            <Grid item xs={12}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
                <Button variant="contained" color="primary" onClick={handleSave}>저장</Button>
                {(role === 'Main Admin' || role === 'Sub Admin') && (
                  <Button variant="contained" color="secondary" onClick={() => { console.log('View Subordinates'); }}>
                    하위 관리자 보기
                  </Button>
                )}
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Box>
  );
};

export default AdminInfo;
