import React, { useState, useEffect } from 'react';
import { Paper, Box, Button, CircularProgress, Typography, TextField, Container, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, TablePagination } from '@mui/material';
import { useAuth } from '../../context/AuthProvider';
import { useNavigate } from 'react-router-dom';

const AdminList = () => {
    const { currentUser, admins } = useAuth();
    const navigate = useNavigate();

    const [visibleAdmins, setVisibleAdmins] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);

    useEffect(() => {
        if (currentUser && admins) {
            const filteredAdmins = currentUser.role === 'Main Admin'
                ? admins
                : admins.filter(admin => currentUser.subordinates.includes(admin.id));
            
            const dataWithActions = filteredAdmins.map(admin => ({
                ...admin,
                actions: (
                    <Button variant="contained" color="primary" onClick={() => handleViewMembers(admin.id)}>
                        View Members
                    </Button>
                )
            }));
            
            setVisibleAdmins(dataWithActions);
            console.log('Filtered Admins:', dataWithActions);
        }
    }, [currentUser, admins]);

    const handleViewMembers = (adminId) => {
        navigate(`/admin-members/${adminId}`);
    };

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value.toLowerCase());
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const filteredVisibleAdmins = visibleAdmins.filter(admin =>
        admin.username.toLowerCase().includes(searchTerm) ||
        admin.role.toLowerCase().includes(searchTerm)
    );

    if (!currentUser || !admins) {
        return (
            <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
                <CircularProgress />
            </Box>
        );
    }

    return (
        <Container maxWidth="lg" sx={{ mt: 4 }}>
            <Paper style={{ padding: '20px' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
                    <Typography variant="h4">Admin Dashboard</Typography>
                    <TextField
                        label="Search Admins"
                        variant="outlined"
                        size="small"
                        value={searchTerm}
                        onChange={handleSearchChange}
                        sx={{ ml: 2 }}
                    />
                </Box>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Username</TableCell>
                                <TableCell>Role</TableCell>
                                <TableCell>Actions</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {filteredVisibleAdmins.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((admin) => (
                                <TableRow key={admin.id}>
                                    <TableCell>{admin.username}</TableCell>
                                    <TableCell>{admin.role}</TableCell>
                                    <TableCell>{admin.actions}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[5, 10, 25]}
                    component="div"
                    count={filteredVisibleAdmins.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                />
            </Paper>
        </Container>
    );
};

export default AdminList;
