const MemberContext = createContext();

export const useMembers = () => useContext(MemberContext);

export const MemberProvider = ({ children }) => {
  const initialMembers = [
    { id: 1, name: '홍길동', phone: '010-1234-5678', vehicle: 'K5', role: '운전자', accident: '없음',  adminId: 7, accidentDetails: { date: '2023-01-01', location: { lat: 37.5665, lng: 126.9780 } } },
    { id: 2, name: '김철수', phone: '010-9876-5432', vehicle: '아반떼', role: '운전자', accident: '경미한 사고', adminId: 7, accidentDetails: { date: '2023-02-15', location: { lat: 37.5678, lng: 126.9880 } } },
    { id: 3, name: '이영희', phone: '010-5678-1234', vehicle: '모닝', role: '운전자', accident: '사고 없음', adminId: 7, accidentDetails: { date: '2023-03-20', location: { lat: 37.5685, lng: 126.9990 } } },
    { id: 4, name: '박보검', phone: '010-4321-8765', vehicle: '테슬라', role: '운전자', accident: '중대한 사고',  adminId: 8, accidentDetails: { date: '2023-04-10', location: { lat: 37.5695, lng: 126.9995 } } },
    { id: 5, name: '최지수', phone: '010-6789-4321', vehicle: '벤츠', role: '운전자', accident: '없음',  adminId: 8, accidentDetails: { date: '2023-05-01', location: { lat: 37.5705, lng: 126.9999 } } },
    { id: 6, name: '이남동', phone: '010-1224-5638', vehicle: 'K5', role: '운전자', accident: '없음',  adminId: 9, accidentDetails: { date: '2023-01-01', location: { lat: 37.5665, lng: 126.972 } } },
    { id: 7, name: '김연수', phone: '010-9876-5137', vehicle: '아반떼', role: '운전자', accident: '경미한 사고', adminId: 9, accidentDetails: { date: '2023-02-15', location: { lat: 37.5678, lng: 126.6880 } } },
    { id: 8, name: '이연희', phone: '010-5678-1234', vehicle: '모닝', role: '운전자', accident: '사고 없음', adminId: 10, accidentDetails: { date: '2023-03-20', location: { lat: 37.5685, lng: 126.9390 } } },
    { id: 9, name: '송연지', phone: '010-4321-8165', vehicle: '테슬라', role: '운전자', accident: '중대한 사고',  adminId: 10, accidentDetails: { date: '2023-04-10', location: { lat: 37.5695, lng: 126.9295 } } },
    { id: 10, name: '지피티', phone: '010-6789-2321', vehicle: '벤츠', role: '운전자', accident: '없음',  adminId: 10, accidentDetails: { date: '2023-05-01', location: { lat: 37.5705, lng: 126.1199 } } }
  ];

// 승인대기하면 그 관리자 번호 선택할 수 있는창 추가?? 아니면 바로 로그인한 아이디가 승인 추가하면 바로 들어가게?
  
const initialPendingMembers = [
  { id: 11, name: '오세훈', phone: '010-1111-2222', vehicle: '소나타', role: '운전자', accident: '없음', adminId: 7, accidentDetails: { date: '', location: { lat: 37.5665, lng: 126.9780 }} },
  { id: 12, name: '강호동', phone: '010-3333-4444', vehicle: '스포티지', role: '운전자', accident: '없음', adminId: 7, accidentDetails: { date: '', location: { lat: '', lng: '' } } }
];



const initialAccidents = [
  { accident_id: 1, member_id: 1, location: { lat: 37.5665, lng: 126.9780 }, date: '2023-01-01', insurance_company_id: 1 },
  { accident_id: 2, member_id: 1, location: { lat: 35.1796, lng: 129.0756 }, date: '2023-02-01', insurance_company_id: 1 },
  { accident_id: 3, member_id: 2, location: { lat: 37.4563, lng: 126.7052 }, date: '2023-03-01', insurance_company_id: 2 },
];
    const [members, setMembers] = useState(() => {
    const storedMembers = localStorage.getItem('members');
    return storedMembers ? JSON.parse(storedMembers) : initialMembers;
  });
  //승인된 멤버들 
  const [pendingMembers, setPendingMembers] = useState(() => {
    const storedPendingMembers = localStorage.getItem('pendingMembers');
    return storedPendingMembers ? JSON.parse(storedPendingMembers) : initialPendingMembers;
  });
//사고
  const [accidents, setAccidents] = useState(() => {
    const storedAccidents = localStorage.getItem('accidents');
    return storedAccidents ? JSON.parse(storedAccidents) : initialAccidents;
  });
  //로컬에 추가
  useEffect(() => {
    localStorage.setItem('members', JSON.stringify(members));
  }, [members]);

  useEffect(() => {
    localStorage.setItem('pendingMembers', JSON.stringify(pendingMembers));
  }, [pendingMembers]);

  useEffect(() => {
    localStorage.setItem('accidents', JSON.stringify(accidents));
  }, [accidents]);


  const addMember = (member) => {
    setPendingMembers([...pendingMembers, { ...member, id: pendingMembers.length + members.length + 1 }]);
  };

  //승인멤버
  const approveMember = (id) => {
    const memberToApprove = pendingMembers.find(member => member.id === id);
    setMembers([...members, memberToApprove]);
    setPendingMembers(pendingMembers.filter(member => member.id !== id));
  };

  const updateMember = (updatedMember) => {
    setMembers(members.map(member => member.id === updatedMember.id ? updatedMember : member));
  };

  const deleteMember = (id) => {
    setMembers(members.filter(member => member.id !== id));
  };

  const addAccident = (accident) => {
    setAccidents([...accidents, { ...accident, accident_id: accidents.length + 1 }]);
  };

  const deleteAccident = (accident_id) => {
    setAccidents(accidents.filter(accident => accident.accident_id !== accident_id));
  };

  return (
    <MemberContext.Provider value={{ members, pendingMembers, accidents, addMember, approveMember, updateMember, deleteMember, addAccident, deleteAccident }}>
      {children}
    </MemberContext.Provider>
  );
};