import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import { useMembers } from '../../context/MemberContext';
import KakaoMap from '../../components/KakaoMap';
import GenericDialog from '../../components/GenericDialog';
import { Paper, Container, Typography, Box, Button, Tabs, Tab, Card, CardContent } from '@mui/material';

const MemberDetails = () => {
  const { id } = useParams();
  const { members, accidents, addAccident } = useMembers();
  const [selectedTab, setSelectedTab] = useState(0);
  const [open, setOpen] = useState(false);
  const [newAccident, setNewAccident] = useState({ member_id: parseInt(id), location: '', date: '', insurance_company_id: '' });

  const member = members.find(m => m.id === parseInt(id));
  const memberAccidents = accidents.filter(accident => accident.member_id === parseInt(id));

  if (!member) {
    return (
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Paper style={{ padding: '20px', maxWidth: '600px', margin: 'auto' }}>
          <Typography variant="h4" component="h2" gutterBottom>
            Member not found.
          </Typography>
        </Paper>
      </Container>
    );
  }

  const handleAccidentClick = (event, newValue) => {
    setSelectedTab(newValue);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    addAccident(newAccident);
    setOpen(false);
  };

  const handleChange = (prop) => (event) => {
    setNewAccident({ ...newAccident, [prop]: event.target.value });
  };

  return (
    <>
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Paper style={{ padding: '20px', maxWidth: '600px', margin: 'auto' }}>
          <Typography variant="h4" component="h2" gutterBottom>
            {member.name}의 사고 정보
          </Typography>
          <Typography variant="body1" component="p" gutterBottom>
            차량: {member.vehicle}
          </Typography>
          <Typography variant="body1" component="p" gutterBottom>
            사고 횟수: {memberAccidents.length}회
          </Typography>
          <Button variant="contained" color="primary" onClick={handleOpen}>
            사고 추가하기
          </Button>
          <Box sx={{ width: '100%', mt: 4 }}>
            <Tabs value={selectedTab} onChange={handleAccidentClick} centered>
              {memberAccidents.map((accident, index) => (
                <Tab label={`사고 ${index + 1}`} key={index} />
              ))}
            </Tabs>
            {memberAccidents[selectedTab] && (
              <>
                <Typography variant="body1" component="p" gutterBottom>
                  사고 날짜: {memberAccidents[selectedTab].date}
                </Typography>
                <Box sx={{ mt: 4 }}>
                  <KakaoMap location={memberAccidents[selectedTab].location} />
                </Box>
              </>
            )}
          </Box>
        </Paper>
      </Container>
      <GenericDialog
        open={open}
        data={newAccident}
        onClose={handleClose}
        onSave={handleSave}
        onChange={handleChange}
        fields={[
          { name: 'location', label: '위치 (위도, 경도)', autoFocus: true },
          { name: 'date', label: '날짜', type: 'date' },
          { name: 'insurance_company_id', label: '보험 회사 ID' },
        ]}
        title="Accident"
      />
    </>
  );
};

export default MemberDetails;
