import React, { useState, useEffect } from 'react';
import { Container, Paper, Box, Button, Typography, IconButton, Snackbar, Chip, Stack, Table, TableBody, TableCell, TableHead, TableRow, TableContainer, TablePagination } from '@mui/material';
import { useAuth } from '../../context/AuthProvider';
import SearchBox from '../../components/SearchBox';
import { Delete as DeleteIcon, Edit as EditIcon, Add as AddIcon } from '@mui/icons-material';
import GenericDialog from '../../components/GenericDialog';
import { useNavigate } from 'react-router-dom';

const Notice = () => {
  const { currentUser } = useAuth();
  const navigate = useNavigate();
  const [notices, setNotices] = useState([
    { id: 1, title: '공지사항 1', content: '내용 1', category: '일반', date: new Date().toLocaleDateString(), pinned: false },
    { id: 2, title: '공지사항 2', content: '내용 2', category: '중요', date: new Date().toLocaleDateString(), pinned: false },
    { id: 3, title: '공지사항 3', content: '내용 3', category: '기타', date: new Date().toLocaleDateString(), pinned: false },
    // 더 많은 공지사항 추가 가능
  ]);

  const [searchTerm, setSearchTerm] = useState('');
  const [searchType, setSearchType] = useState('title');
  const [categoryFilter, setCategoryFilter] = useState('all');
  const [page, setPage] = useState(1);
  const [open, setOpen] = useState(false);
  const [currentNotice, setCurrentNotice] = useState(null);
  const [alertOpen, setAlertOpen] = useState(false);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  useEffect(() => {
    // 페이지네이션 초기화
    setPage(0);
  }, [searchTerm, searchType]);

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value.toLowerCase());
  };

  const filteredNotices = notices.filter(notice => 
    notice[searchType].toLowerCase().includes(searchTerm.toLowerCase()) &&
    (categoryFilter === 'all' || notice.category === categoryFilter)
  );

  const handleClickOpen = (notice) => {
    setCurrentNotice(notice || { title: '', content: '', category: '일반', date: new Date().toLocaleDateString(), pinned: false });
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    if (!currentNotice.title || !currentNotice.content) {
      setAlertOpen(true);
      return;
    }
    if (currentNotice.id) {
      setNotices(notices.map(notice => notice.id === currentNotice.id ? currentNotice : notice));
    } else {
      setNotices([...notices, { ...currentNotice, id: notices.length + 1 }]);
    }
    setOpen(false);
  };

  const handleChange = (name) => (event) => {
    setCurrentNotice({ ...currentNotice, [name]: event.target.value });
  };

  const handleDelete = (id) => {
    setNotices(notices.filter(notice => notice.id !== id));
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  const handlePin = (id) => {
    setNotices(notices.map(notice => 
      notice.id === id ? { ...notice, pinned: !notice.pinned } : notice
    ));
  };

  const handleTitleClick = (notice) => {
    navigate(`/notice/${notice.id}`, { state: { notice } });
  };

  const handleCategoryFilterChange = (category) => {
    setCategoryFilter(category);
  };
  const searchTypes = [
    { value: 'title', label: 'Title' },
    { value: 'category', label: 'Category' }
  ];

  const dataToShow = filteredNotices.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);
  const count = Math.ceil(filteredNotices.length / rowsPerPage);
  const fields = [
    { name: 'title', label: 'Title', autoFocus: true },
    { name: 'content', label: 'Content', multiline: true, rows: 4 },
    {
      name: 'category',
      label: 'Category',
      type: 'radio',
      options: ['일반', '중요', '기타']
    }
  ];

  return (
    <Container maxWidth="lg" sx={{ mt: 4 }}>
      <Paper style={{ padding: '20px' }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
          <SearchBox
            searchTerm={searchTerm}
            setSearchTerm={setSearchTerm}
            searchType={searchType}
            setSearchType={setSearchType}
            searchTypes={searchTypes}
          />
        {currentUser && (currentUser.role === 'Main Admin' || currentUser.role === 'Sub Admin') && (
            <Button variant="contained" color="primary" startIcon={<AddIcon />} onClick={() => handleClickOpen()}>
              Add Notice
            </Button>
          )}
        </Box>
        <Stack direction="row" spacing={1} sx={{ marginBottom: 2 }}>
          <Chip label="All" onClick={() => handleCategoryFilterChange('all')} color={categoryFilter === 'all' ? 'primary' : 'default'} />
          <Chip label="일반" onClick={() => handleCategoryFilterChange('일반')} color={categoryFilter === '일반' ? 'primary' : 'default'} />
          <Chip label="중요" onClick={() => handleCategoryFilterChange('중요')} color={categoryFilter === '중요' ? 'primary' : 'default'} />
          <Chip label="기타" onClick={() => handleCategoryFilterChange('기타')} color={categoryFilter === '기타' ? 'primary' : 'default'} />
        </Stack>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Title</TableCell>
                <TableCell>Content</TableCell>
                <TableCell>Category</TableCell>
                <TableCell>Date</TableCell>
                {currentUser && (currentUser.role === 'Main Admin' || currentUser.role === 'Sub Admin') && <TableCell>Actions</TableCell>}
              </TableRow>
            </TableHead>
            <TableBody>
              {dataToShow.map(notice => (
                <TableRow key={notice.id}>
                  <TableCell> 
                    <Typography 
                      variant="body2" 
                      color="primary" 
                      onClick={() => handleTitleClick(notice)}
                      sx={{ cursor: 'pointer' }}
                    >
                      {notice.title}
                    </Typography>
                    </TableCell>
                  <TableCell>{notice.content}</TableCell>
                  <TableCell>{notice.category}</TableCell>
                  <TableCell>{notice.date}</TableCell>
                  {currentUser && (currentUser.role === 'Main Admin' || currentUser.role === 'Sub Admin') && (
                    <TableCell>
                      <IconButton size="small" color="secondary" onClick={() => handleClickOpen(notice)}>
                        <EditIcon />
                      </IconButton>
                      <IconButton size="small" color="error" onClick={() => handleDelete(notice.id)}>
                        <DeleteIcon />
                      </IconButton>
                      <IconButton size="small" color="primary" onClick={() => handlePin(notice.id)}>
                        {notice.pinned ? '📌' : '📍'}
                      </IconButton>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={filteredNotices.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
        <GenericDialog
          open={open}
          data={currentNotice}
          onClose={handleClose}
          onSave={handleSave}
          onChange={handleChange}
          fields={fields}
          title="Notice"
        />
        <Snackbar open={alertOpen} autoHideDuration={6000} onClose={() => setAlertOpen(false)} message="Please fill in all required fields." />
      </Paper>
    </Container>
  );
};

export default Notice;