import React, { useState, useEffect } from 'react';
import { Container, Typography, Button, Box, Paper, List, ListItem, ListItemText, IconButton, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { useAuth } from '../context/AuthProvider'; // 로그인 상태를 확인하기 위한 훅
import SearchBox from './SearchBox';

const ComplaintSection = ({ searchTerm, setSearchTerm, searchType, setSearchType, searchTypes }) => {
  const { currentUser } = useAuth(); // 로그인한 사용자 정보
  const [complaints, setComplaints] = useState([]);
  const [open, setOpen] = useState(false);
  const [currentComplaint, setCurrentComplaint] = useState({ title: '', description: '' });

  useEffect(() => {
    // 초기 데이터 로드
    const savedComplaints = localStorage.getItem('complaints');
    if (savedComplaints) {
      setComplaints(JSON.parse(savedComplaints));
    }
  }, []);

  useEffect(() => {
    // 데이터가 변경될 때마다 로컬 스토리지에 저장
    localStorage.setItem('complaints', JSON.stringify(complaints));
  }, [complaints]);

  const handleOpen = (complaint = { title: '', description: '' }) => {
    setCurrentComplaint(complaint);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    if (!currentComplaint.title || !currentComplaint.description) {
      alert('Please fill in all fields.');
      return;
    }
    if (currentComplaint.id) {
      setComplaints(complaints.map(c => c.id === currentComplaint.id ? currentComplaint : c));
    } else {
      setComplaints([...complaints, { ...currentComplaint, id: complaints.length + 1, user: currentUser.username }]);
    }
    handleClose();
  };

  const handleDelete = (id) => {
    setComplaints(complaints.filter(c => c.id !== id));
  };

  const handleChange = (prop) => (event) => {
    setCurrentComplaint({ ...currentComplaint, [prop]: event.target.value });
  };

  const filteredComplaints = complaints.filter(c =>
    c.user === currentUser.username && (c[searchType].toString().toLowerCase().includes(searchTerm.toLowerCase()))
  );

  return (
    <Container maxWidth="md">
      <Typography variant="h4" component="h2" gutterBottom>Customer Complaints</Typography>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
        <SearchBox
          searchTerm={searchTerm}
          setSearchTerm={setSearchTerm}
          searchType={searchType}
          setSearchType={setSearchType}
          searchTypes={searchTypes}
        />
        <Button variant="contained" color="primary" onClick={() => handleOpen()}>Add Complaint</Button>
      </Box>
      <List>
        {filteredComplaints.map((complaint) => (
          <ListItem key={complaint.id} component={Paper} sx={{ marginBottom: 2 }}>
            <ListItemText primary={complaint.title} secondary={complaint.description} />
            <IconButton edge="end" aria-label="edit" onClick={() => handleOpen(complaint)}>
              <EditIcon />
            </IconButton>
            <IconButton edge="end" aria-label="delete" onClick={() => handleDelete(complaint.id)}>
              <DeleteIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{currentComplaint.id ? 'Edit Complaint' : 'Add Complaint'}</DialogTitle>
        <DialogContent>
          <TextField autoFocus margin="dense" label="Title" type="text" fullWidth variant="standard" value={currentComplaint.title} onChange={handleChange('title')} />
          <TextField margin="dense" label="Description" type="text" fullWidth variant="standard" value={currentComplaint.description} onChange={handleChange('description')} />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSave}>Save</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default ComplaintSection;
