import React from 'react';
import { AuthProvider } from './context/AuthProvider';
import { BrowserRouter as Router, Routes, Route, Link, Navigate  } from 'react-router-dom';
import Dashboard from './components/DashBoard';
import LoginPage from './pages/login/login';
import AdminList from './pages/admin/AdminList';
import MemberDetails from './pages/member/MemberDetails';
import MemberList from './pages/member/MemberList';
import CustomerService from './pages/customer/CustomerService';
import { ThemeProvider } from '@mui/material/styles';
import theme from './styles/theme';
import { UserProvider } from './context/UserContext';
import { MemberProvider } from './context/MemberContext';
import Notice from './pages/notice/Notice';
import InsurancePage from './pages/Insurance/InsurancePage';
import AdminInfo from './pages/admin/AdminInfo';
import NoticeDetail from './pages/notice/NoticeDetail';
import AdminMembers from './pages/admin/AdminMembers';

function App() {
  
  return (
  <UserProvider>
    <ThemeProvider theme={theme}>
      <Router basename="/myren">
        <AuthProvider>
          <MemberProvider>
            <Dashboard />
            <Routes>
              <Route path="/" element={<Navigate replace to="/login" />} />
              <Route path="/admins" element={<AdminList />} />
              <Route path="/admin-info" element={<AdminInfo />} />
              <Route path="/admin-members/:adminId" element={<AdminMembers />} />
              {/* <Route path="/admin-members/:adminId" element={<MemberList />} /> */}
              <Route path="/customer-service" element={<CustomerService />} />
              <Route path="/login" element={<LoginPage />} />
              <Route path="/members" element={<MemberList />} />
              <Route path="/member-details/:id"  element={<MemberDetails />} />
              <Route path="/notice"  element={<Notice />} />
              <Route path="/notice/:id" element={<NoticeDetail />} />
              <Route path="/insurance" element={<InsurancePage />} />
              <Route path="*" element={<div>404 Not Found</div>} />
            </Routes>
          </MemberProvider>
        </AuthProvider>
      </Router>
    </ThemeProvider>
  </UserProvider>
  );
}

export default App;